	module.exports = {"data":{"markdownRemark":{"html":"<p>イタリアのレストランで日本国際フォーラムで日本の学者と朝食を取る。期間中、私はいくつかの問題について話し、以下のように要約しました：</p>\n<p>最近、長野県法務大臣の「南京大虐殺」に対する姿勢は、この問題について日本の政治家の見解を反映しているが、国際社会の圧力の下で与野党は譲歩することができず、 。 Yongyeの友人は、Yongyeが日本で彼に反対するいくつかの記者によって設定されたトラップに落ちたと言いました。しかし、 \"南京大虐殺\"を認めた。新聞によると、永慶（ヨンギ）は「南京大虐殺」自体を否定したと報じた。これは説明でもあります。いずれにせよ、この事件は、多くの日本の高級人がこの問題を深く理解していないことを反映しています。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0090.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0090.md","frontmatter":{"title":"5月15日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0090.md absPath of file >>> MarkdownRemark"}}

